<template>
  <div id="app">
    <div class="text-center">
      <v-snackbar
        bottom
        color="success"
        rounded="lg"
        class="mb-2"
        icon="mdi-delete-circle"
        v-model="AlertBerhasilTerubah"
        :timeout="timeout"
      >
        <v-progress-circular
          size="35"
          width="4"
          class="mt-n2 mr-3"
          color="white"
          indeterminate
        >
          <v-icon>mdi-check-bold</v-icon>
        </v-progress-circular>
        <span class="title">Diubah</span>
        <template v-slot:action="{ attrs }">
          <v-btn
            text
            v-bind="attrs"
            @click="AlertBerhasilTerubah = false"
          >
            Tutup
          </v-btn>
        </template>
      </v-snackbar>
    </div>

    <!-- <div class="text-center">
      <v-snackbar
        bottom
        color="error"
        rounded="lg"
        class="mb-2"
        icon="mdi-delete-circle"
        v-model="AlertDataTerhapus"
        :timeout="timeout"
      >
        <v-progress-circular
          size="35"
          width="4"
          class="mt-n2 mr-3"
          color="white"
          indeterminate
        >
          <v-icon>mdi-check-bold</v-icon>
        </v-progress-circular>
        <span class="title">Dihapus</span>
        <template v-slot:action="{ attrs }">
          <v-btn
            text
            v-bind="attrs"
            @click="AlertDataTerhapus = false"
          >
            Tutup
          </v-btn>
        </template>
      </v-snackbar>
    </div> -->

    <!-- Alert Tidak Punya Akses Halaman -->
    <v-col v-show="this.valid == false">
      <v-alert text type="error">
        Kamu tidak punya akses Ke halaman "Perusahaan"
      </v-alert>
    </v-col>

    <!-- Alert Data Tersimpan -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      v-model="Alert"
      class="rounded-lg"
      icon="mdi-check-bold"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      Data Berhasil Disimpan
    </v-alert>

    <!-- Alert Data Berhasil Terubah -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      class="rounded-lg"
      icon="mdi-check-bold"
      v-model="AlertBerhasilTerubah"
      transition="slide-x-transition"
    >
      Data Berhasil Diubah
    </v-alert>

    <!-- Alert Data Sudah Terhapus -->
    <!-- <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      color="error"
      elevation="4"
      class="rounded-lg"
      icon="mdi-delete"
      v-model="AlertDataTerhapus"
      transition="slide-x-transition"
    >
      Data Berhasil Dihapus
    </v-alert> -->

    <!-- ---------------------------------------------------- -->

    <div v-show="loading == true">
      <v-progress-linear
        :active="loading"
        :indeterminate="loading"
        absolute
        top
        height="6"
        color="red darken-4"
      ></v-progress-linear>
    </div>

    <v-container fluid v-show="this.valid == true">
      <v-toolbar
        flat
        height="55"
        :color="$vuetify.theme.dark ? '' : 'grey lighten-2'"
        class="mb-3 rounded-lg"
        v-resize="onResize"
      >
        <v-toolbar-title>Data Perusahaan</v-toolbar-title>

        <v-spacer></v-spacer>

        <!-- Menu Export Data -->
        <div class="text-center mx-2">
          <v-menu
            v-model="MenuExport"
            :close-on-content-click="false"
            :nudge-width="1"
            rounded="lg"
            offset-y
          >
            <template v-slot:activator="{ on, attrs }">
              <!-- Btn Export Mode Biasa -->
              <v-btn
                @click="MenuExport = !MenuExport"
                class="text-capitalize rounded-lg"
                v-show="mobile == false"
                v-bind="attrs"
                v-on="on"
                outlined
                text
                :disabled="loading == true"
              >
                <v-icon left>mdi-table-arrow-down</v-icon>
                <span class="subtitle-1 mr-n1">Export Data</span>
                <v-icon right>{{ MenuExport ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
            </template>

            <v-card class="pa-2" v-show="MenuExport" :outlined="$vuetify.theme.dark ? true : false">   
              <!-- PDF -->
              <v-btn
                block
                depressed
                @click.native="PdfExport"
                class="text-capitalize subtitle-1 rounded-lg mb-2"
              >
                Pdf
              </v-btn>
            
              <!-- EXCEL -->
              <v-btn
                block
                depressed                       
                @click.native="excelExport"
                class="text-capitalize subtitle-1 rounded-lg"
              >
                Excel
              </v-btn>
            </v-card>
          </v-menu>

          <!-- Btn Export Dialog Mode Mobile -->
          <v-dialog
            v-model="DialogPilihExport"
            max-width="200"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-show="mobile == true"
                v-bind="attrs"
                v-on="on"
                outlined
                x-small text
                class="rounded-lg"
                height="40px" width="40px"
                :disabled="loading == true"
              >
                <v-icon>mdi-table-arrow-down</v-icon>
              </v-btn>
            </template>
            <v-card class="rounded-lg">
              <v-toolbar dense>
                <v-layout justify-center>
                  <v-toolbar-title>
                    Pilih Export File
                  </v-toolbar-title>
                </v-layout>
              </v-toolbar>
              <v-card class="pa-2">
                <v-btn text block outlined class="text-capitalize subtitle-1 mb-2" @click.native="PdfExport">
                  Pdf
                  <v-icon right>mdi-adobe-acrobat</v-icon>
                </v-btn>

                <v-btn text block outlined class="text-capitalize subtitle-1" @click.native="excelExport">
                  Excel
                  <v-icon right>mdi-microsoft-excel</v-icon>
                </v-btn>
              </v-card>
            </v-card>
          </v-dialog>
        </div>

        <!-- Dialog Tambah User -->
        <v-dialog
          v-model="DialogTambahPerusahaan"
          max-width="550px"
          scrollable
          persistent
        >
          <template v-slot:activator="{ on, attrs }">
            <!-- Btn Tambah Mode Desktop -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="loading == true"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Desktop Kondisi Tidak bisa tambah user -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == false"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="ValidCreate == false"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Mobile -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="loading == true"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
            
            <!-- Btn Tambah Mode Mobile Kondisi Tidak bisa tambah user -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == false"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="ValidCreate == false"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
          </template>
          <v-card outlined class="rounded-lg">
            <v-toolbar flat>
              <v-toolbar-title>
                <span>{{ formTitlePerusahaan }}</span>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn
                icon
                @click="KeluarDialogPerusahaan"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar>

            <v-divider></v-divider>
            
            <v-card-text>
              <div class="mr-n3 ml-n3 mb-n2 mt-3">
                <v-sheet outlined class="rounded-lg pa-2">
                  <div class="pa-2">
                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kode Perusahaan</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.KodePerusahaan"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Nama Perusahaan</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.NamaPerusahaan"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-text-field>
                      </v-col>
                    </v-row>

                    <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Alamat</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                        <v-textarea
                          dense
                          no-resize
                          rows="2"
                          color="dark"
                          v-model="editedItem.Alamat"
                        >
                          <template v-slot:prepend v-if="mobile == false">
                            <span>:</span>
                          </template>
                        </v-textarea>
                      </v-col>
                    </v-row>
                  </div>
                </v-sheet>
                
                <!-- <v-sheet outlined class="rounded-lg pa-5">
                  <v-row> 
                    <v-col cols="12" sm="12" md="12" lg="12">
                      <v-text-field
                        dense
                        label="Kode Perusahaan"
                        color="dark"
                        v-model="editedItem.KodePerusahaan"
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12" md="12" lg="12">
                      <v-text-field
                        dense
                        label="Nama Perusahaan"
                        color="dark"
                        v-model="editedItem.NamaPerusahaan"
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12" md="12" lg="12">
                      <v-textarea
                        dense
                        color="dark"
                        v-model="editedItem.Alamat"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-sheet> -->
              </div>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <span class="d-inline-block text-truncate">
                Status : <span>{{Status}}</span>
              </span>
              
              <v-spacer></v-spacer>

              <div class="mr-n2">
                <v-btn
                  dark
                  depressed
                  :color="$vuetify.theme.dark ? 'white black--text' : 'grey darken-3'"
                  class="text-capitalize subtitle-1 rounded-lg"
                  @click="Simpan"
                >
                  <span>Simpan</span>
                </v-btn>
              </div>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>

      <v-card outlined class="pa-2 rounded-lg" :color="$vuetify.theme.dark ? '' : 'grey lighten-2'">
        <div>
          <ejs-grid
            ref='gridperusahaan'
            style="height: 100%;"
            :height="mobile ? '43vh' : '41vh'"
            gridLines='Both'
            id="perusahaanview"
            :dataSource="DataPerusahaan"
            :allowPaging="true"
            :allowSorting='true'
            :allowResizing='true'
            :allowGrouping='true'
            :toolbar='toolbarOptions'
            :commandClick="commandClick"
            :pageSettings='pageSettings'
            :allowExcelExport='true'
            :allowPdfExport='true'
          >
            <e-columns>
              <e-column headerText="Aksi" textAlign='Center' width="120" :commands="commands" :lockColumn='true' :customAttributes="customAttributes">
                <div class="btn-group">
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowEdit($event)">
                    <i class="fa fa-pencil"></i>
                  </v-btn>
                  <v-btn type="button" class="btn btn-default" (click)="prediemRowDelete($event)">
                    <i class="fa fa-trash"></i>
                  </v-btn>
                </div>
              </e-column>
              <e-column field='KodePerusahaan' headerText='Kode Perusahaan' textAlign='Left' width=170></e-column>
              <e-column field='NamaPerusahaan' headerText='Nama Perusahaan' width=300></e-column>
              <e-column field='Alamat' headerText='Alamat' width=150></e-column>
              <e-column field='DiBuatOleh' headerText='DibuatOleh' textAlign='Left' width=150></e-column>
              <e-column field='DiBuatTgl' headerText='DibuatTgl' textAlign='Left' width=150></e-column>
              <e-column field='DiUbahOleh' headerText='DiubahOleh' textAlign='Left' width=150></e-column>
              <e-column field='DiUbahTgl' headerText='DiubahTgl' textAlign='Left' width=150></e-column>
            </e-columns>
          </ejs-grid>
        </div>
      </v-card>
    </v-container>
  </div>
</template>
<script>
import Vue from "vue";
import { GridPlugin, Page, Sort, Group, Resize, Toolbar, Search, CommandColumn, ExcelExport, PdfExport } from "@syncfusion/ej2-vue-grids";
import api from "@/services/http";
Vue.use(GridPlugin);

export default {
  data() {
    return {
      snackbar: false,
      timeout: 15000,
      mobile:null,
      valid:null,
      Permission:[],
      ValidCreate:false,
      passbaru:null,
      KodePerusahaan:null,
      textAktif:true,
      umenu:[],
      verifpassbaru:null,
      loading:true,
      Kode:null,
      DialogPilihExport:false,
      windowSize: {x: 0, y: 0},
      MenuExport: false,
      commands: [],
      customAttributes : {class: 'customcss'},
      Alert:false,
      akun:[],
      usermenu:null,
      AlertDataTerhapus:false,
      AlertBerhasilTerubah:false,
      user:[],
      token:null,
      DialogTambahPerusahaan: false,
      form: new FormData,
      editedIndex: -1,

      defaultItem: {
        id:"",
        KodePerusahaan: "",
        NamaPerusahaan: "",
        Alamat: ""
      },
      editedItem: {
        id:"",
        KodePerusahaan: "",
        NamaPerusahaan: "",
        Alamat: ""
      },
      toolbarOptions: ['Search'],
      DataPerusahaan: [],
      pageSettings: {pageSize: 10, pageSizes :['5','10','15','20','50','All']},
    };
  },
  

  provide: {
    grid: [Page, Sort, Group, Resize, Toolbar, CommandColumn, Search,ExcelExport,PdfExport ]
  },

 async mounted(){
    this.token = localStorage.getItem('token')
    this.user = JSON.parse(localStorage.getItem('user'))
    // await Promise.all([this.saya()])
    if (this.Permission.some(a => a == 'R')) {
      this.valid = true
      this.getdata()
    }else{
      this.loading = false
      this.valid = false
    }
    if (this.Permission.some(a => a == 'C')) {
     this.ValidCreate = true
    }
    if (this.Permission.some(a => a == 'U')) {
     this.commands.push({buttonOption: { cssClass: "e-flat Edit", iconCss: "e-edit e-icons", content:' '} })
    }
    if (this.Permission.some(a => a == 'D')) {
     this.commands.push({ buttonOption: { cssClass:"e-flat Delete", iconCss: "e-delete e-icons", content:' '} })
    }
  },
  
  created(){
    //get action for this page
    console.log('param',this.$route.params.Permission)
    let permisi = this.$route.params.Permission
    this.Permission = permisi == null ? [] : permisi.split(',')
    },

  computed: {
    formTitlePerusahaan() {
      return this.editedIndex === -1 ? 'Tambah Data Perusahaan' : 'Ubah Data Perusahaan'
    },
    Status () {
      return this.editedIndex === -1 ? 'Baru' : 'Ubah'
    },
  },
  
  watch: {
  
      // let a = this.DataPerusahaan.filter( function(item){return (item.UserMenu == newval.toUpperCase());} )
      // this.umenu = a
    windowSize(){
      if (this.windowSize.x < 450) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },

    DialogTambahPerusahaan (val) {
      val || this.KeluarDialogPerusahaan()
    },

    Alert(){
      this.AlertItem()
    },
    AlertDataTerhapus(){
      this.AlertItem()
    },
    AlertBerhasilTerubah(){
      this.AlertItem()
    },
  },

  methods: {
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    cariKode(Kode){
      if (Kode != null) {
          this.textAktif = false
        }else{
          this.textAktif = true
        }
      if (Kode == null || Kode == '' || Kode.length == 0) {
        this.editedIndex = -1
          this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      }else{
      this.Kode = Kode.toUpperCase()
      let a = this.DataPerusahaan.filter( function(item){return (item.Kode == Kode.toUpperCase());} );
      if (a.length > 0) {
          this.editedIndex = 1;
          this.textAktif = false
          this.editedItem.Nama = a[0].Nama
          // this.editedItem.UserMenu = a[0].UserMenu
        }else{
          //do nothing
        }
      }
    },
  async saya(){
       if (this.$menu == undefined) {
         await api.get('/menu?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
        res=>{
          if (res) {
            this.akun = res.data
            let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
            //do noting
          }
        }).catch(err=>{
          // alert(err)
          if (err == "Error: Request failed with status code 401" && this.$route.path != "/login") {
            this.logout()
          }
        })
       }else{
        this.akun = this.$menu
        let route = this.$route.name
            let a = this.akun.filter( function(item){return (item.Route == route);} );
            if (a.length > 0) {
               this.valid = true
            }else{
              this.valid = false
              this.loading = false
            }
       }     
    },
      // cekmenu(){
        
      // },

    Simpan(){
      if(this.formTitlePerusahaan === "Tambah Data Perusahaan"){
        let data = {
          KodePerusahaan: this.editedItem.KodePerusahaan,
          NamaPerusahaan: this.editedItem.NamaPerusahaan,
          Alamat: this.editedItem.Alamat,
        }
        let encryptedata = this.$encrypt(data)
        this.Toast('Data Disimpan')
          api.post('/perusahaan?token='+this.token,{
            data:encryptedata
            },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
            .then((res)=>{
              if (res) {
                // this.Alert = true
                this.getdata()
              } else {
                //
              }
            })
      }else if(this.formTitlePerusahaan === "Ubah Data Perusahaan"){
        let data = {
          KodePerusahaan: this.editedItem.KodePerusahaan,
          NamaPerusahaan: this.editedItem.NamaPerusahaan,
          Alamat: this.editedItem.Alamat,
        }
        let encryptedata = this.$encrypt(data)
        this.Toast('Data Diubah')
         api.put("/perusahaan/"+this.editedItem.id+'/?token='+this.token,{
            data:encryptedata
					},
          { headers: {"Authorization" : `Bearer ${this.token}` } })
					.then((res) => {
						if (!res) {
							//do nothing
						}else{
              this.getdata()
              // this.AlertBerhasilTerubah = true
						}
						
					})
      }
      this.KeluarDialogPerusahaan()    
    },

    commandClick: function(args) {
      if (args.target.classList.contains("custombutton")){
        // let data = JSON.stringify(args.rowData);
		
        // console.log(args.rowData);
      } else if (args.target.classList.contains("Delete")){
        var r = confirm("Yakin Hapus Data?");
        if (r == true) { 
          this.Toast('Data Dihapus')
          api
            .delete("/perusahaan/"+args.rowData.id+'?token='+this.token)
            .then((res) =>{
              // console.log(res);
              if (res) {
                this.getdata()
              this.AlertDataTerhapus = true
              }
              
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else if (args.target.classList.contains("Edit")){
        this.KodePerusahaan = args.rowData.KodePerusahaan
        let data = args
            this.editedIndex = 1;
            // console.log(data)
            this.editedItem = data.rowData
            this.DialogTambahPerusahaan = true

          // if (args.rowData.Aktif == 0) {
          //   this.dataTemp = args.rowData
          //   this.dialogAktifData = true
          //   }else{
          //       let data = args;
          //       this.editedIndex = 1;
          //       this.Kode = data.rowData.Kode
          //       this.editedItem.Nama = data.rowData.Nama
          //       this.editedItem.Password = data.rowData.Password
          //       this.editedItem.UserMenu = data.rowData.UserMenu
          //       // this.dialogWO = true;
          //   }
      }
    },

    PdfExport(){
      let data = document.getElementById('perusahaanview').ej2_instances[0];
      // console.log(data)
      data.pdfExportProperties = {
        pageOrientation: 'Landscape',
        fileName:"ReportPerusahaan.pdf"
      }
      data.columns[1].visible = false
      data.columns[4].visible = false
      data.columns[3].visible = false
      // let pdfExportProperties = {
       
      // };
      // this.$refs.gridperusahaan.columns[0].visible = false;
      // this.$refs.gridperusahaan.columns[1].visible = true;
      this.$refs.gridperusahaan.pdfExport(data.pdfExportProperties);
    },
    excelExport(){
      this.$refs.gridperusahaan.excelExport();
      // console.log(this.$refs.gridperusahaan)
      // let data = document.getElementById('perusahaanview').ej2_instances[0];
      // console.log(data)
      // data.excelExport();
    },

    KeluarDialogPerusahaan () {
      this.DialogTambahPerusahaan = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.Kode = null
        this.textAktif = true
      })
    },

    // beforeExport: function(args) {
      
    // },

    getdata(){
      api.get('/perusahaan?token='+this.token,{
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.loading = false
            this.DataPerusahaan = this.$decrypt(data)
          })
        },
				err => {
					console.log(err);
				}
			) 
    },

    Reset() {
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },
    logout(){
        api.post('/logout', this.token)
        .then(res=>{
          // console.log(res)
          if (res) {
            //do nothing
          }
        })
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        location.reload(false)
      },
    
    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },

    AlertItem(){
      window.setInterval(() => {
        this.Alert = false;
        this.AlertDataTerhapus = false;
        this.AlertBerhasilTerubah = false;
      }, 3000)
    },
  },
}
</script>

